.root {
  padding: 12px;
  display: flex;
  flex-wrap: wrap;
}

.currency-rate-string {
  display: flex;
  padding: 16px 0;
  max-width: 400px;
}

.new-currency1,
.new-currency2 {
  display: flex;
  justify-items: center;
  flex-grow: 1;
}

.currency-rate-string-text {
  font-size: 25px;
  margin-right: 4px;
}

.currency-rate-string-inner {
  display: flex;
  flex-grow: 1;
  margin-right: 4px;
}

.need-row-text {
  font-size: 20px;
  margin: 0 16px;
}

.exhcange-rates,
.right-block {
  width: 420px;
  margin-top: 20px;
  flex-grow: 1;
  max-width: 100%;
}

.input {
  max-width: 60px;
  font-size: 20px;
  height: 36px;
  margin-right: 4px;
  border-radius: 4px;
  padding: 0 0 0 4px;
  border: 1px solid hsl(0, 0%, 80%);
}

.input:focus {
  border: 1px solid #2684ff;
  box-shadow: 0 0 0 1px #2684ff;
  outline: none;
}

.need-row {
  display: flex;
  align-items: center;
  margin-top: 4px;

  .input {
    max-width: 80px;
  }
}

.result-row {
  padding: 8px 0;
}

.rate-row {
  display: flex;
  padding-bottom: 4px;
}

.rate-row-from {
  font-size: 25px;
  width: 150px;
  text-align: start;
}

.rate-row-values {
  margin-left: 4px;
}

.commission-block {
  margin-bottom: 16px;
}

.select {
  min-width: 130px;
  max-width: 130px;
  flex-grow: 1;
}


.currency-rate-string {
  flex-direction: column;
}

.currency-rate-string-inner {
  justify-content: space-between;
}

button {
  height: 38px;
  margin-top: 4px;
}
